  componentDidMount() {
    const {match: {params: {id}}} = this.props
    this._getAsyncData(id)
  }
  // tabskey
  _getAsyncData(type) {
    try {
      let ids = [],
        reqData = []
      if (hasVariant(ORDER_LIST)) {
        ids = getVariant(ORDER_LIST)
      } else {
        removeVariant(ORDER_LIST)
      }
      if (!ids.length) return (0, this._closeWindow)(message.error(''))
      if (type == 1) {
        if (Object.prototype.toString.call(ids[0]) === '[object Object]') {
          this.isFromPackList = true
          reqData = ids
        } else {
          ids.map((o) => {
            reqData.push({
              waveCode: o
            })
          })
        }
      } else {
        ids.map((o) => {
          reqData.push({
            waveId: o
          })
        })
      }
      const {handleGetDetailPrint} = this.props
      const filterZero = getVariant('wms-print-filterZero')
      if (type == 2) {
        if (filterZero) {
          handleGetDetailPrint({data: reqData, searchParams: {filterZero: 'Y'}}, type)
        } else {
          handleGetDetailPrint({data: reqData, searchParams: {filterZero: 'N'}}, type)
        }
      } else {
        handleGetDetailPrint({data: reqData}, type, this.isFromPackList)
      }
    } catch (err) {
      console.error(err)
    }
  }
  /*  */
  _closeWindow = () => window.setTimeout(() => window.close(), 2000)
  /*  */
  handlePrint = () => {
    if (window.print) {
      window.print()
    } else {
      document.execCommand('print')
    }
    try {
      let ids = [],
        reqData = []
      const {match: {params: {id}}} = this.props
      if (hasVariant(ORDER_LIST)) {
        ids = getVariant(ORDER_LIST)
      } else {
        removeVariant(ORDER_LIST)
      }
      if (!ids.length) return (0, this._closeWindow)(message.error(''))
      ids.map((o) => {
        reqData.push({
          waveId: o
        })
      })
      const {handleUpdatePrintNum} = this.props
      handleUpdatePrintNum({data: reqData}, id)
    } catch (err) {
      console.error(err)
    }
  }

